.About-Me {
    /* min-height: 100vh; */
    height: 90vh;
    transition: color 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
    font-family: Montserrat, sans-serif;
    font-weight: 900;
    align-items: center;
}

.About-Me-container {
    display: block;
    margin: auto;
    background-color: #ff9900d2;
    border: 0.1rem solid #ff9800;
    border-radius: 1vh;
    clip-path: polygon(0 0, 100% 0, 100% 80%, 0 100%);
}

.About-Me-heading {
    font-size: 5vh;
    color: #07071d;
    text-align: center;
    letter-spacing: 0.3vw;
    margin-top: 3vh;
}

.About-Me-divider {
    max-width: 100%;
    margin: 0vw 6vw;
    border: 0.2vw solid #07071d;
    border-radius: 0.4vw;
    color: #07071d;
}

.About-Me-body {
    display: flex;
    margin: 5vh 5vw;
    border-radius: 5vh;
    padding: 4vh 2vw;
    overflow: visible;
    word-break: keep-all;
    color: #07071d;
}

.About-Me-info {
    margin: auto;
    text-align: justify;
    font-weight: 500;
}

#About-Me-infoText {
    letter-spacing: 0.1vw;
    line-height: 1.5;
    margin-bottom: 5vh;
    text-align: center;
    padding: auto;
}

#About-Me-image {
    height: 55vh;
    width: 18vw;
    min-width: 12rem;
    min-height: 12rem;
    border-radius: 5vh;
    margin-right: 2.5vw;
    border: 0.5vh solid #07071d;
}

#About-Me-Button {
    color: #07071d;
    font-size: 2vw;
    padding: 0.8vh 0.8vw;
    border: solid;
    border-color: cubic-bezier(0.075, 0.82, 0.165, 1);
    text-align: center;
    transition: background-color 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
    border-radius: 0.6rem;
    text-decoration: none;
    font-weight: 600;
}

#About-Me-Button:hover {
    background-color: #07071d;
    color: #ff9800;
    cursor: pointer;
    border-color: #07071d;
}

#About-Me-skillsHead {
    display: flex;
    justify-content: center;
    margin-top: 5vh;
    font-size: 3vh;
    font-weight: 600;
}

.About-Me-skillItem {
    margin: 0vh 2vw;
    font-style: italic;
    font-weight: 600;
}

#About-Me-skills {
    justify-content: center;
    display: flex;
    margin-top: 1vh;
}

#About-Me-ButtonDiv {
    display: flex;
    justify-content: center;
}

.Current-Status-Label {
    font-weight: 700;
}

.Downwards-Arrow {
    justify-content: center;
    align-items: center;
    display: flex;
}

.Downwards-Arrow:hover {
    cursor: pointer;
}
